* {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.home {
  display: flex;
  height: 100vh;
  width: 100vw;
  background-color: rgb(0, 0, 0);
  font-size: 3vh;
  font-family: monospace;
  font-variant: small-caps;
  color: white;
  align-items: center;
  justify-content: center;
}

.pushable {
  background: rgb(161, 0, 54);
  border-radius: 50%;
  height: 10vw;
  width: 10vw;
  border: none;
  padding: 0;
  cursor: pointer;
  outline-offset: 4px;
  box-shadow: inset 10px -1px 4px 2px rgb(113, 25, 47), 0 0 5px 2px rgb(0, 0, 0);
}
.front {
  display: flex;
  height: 10vw;
  width: 10vw;
  border-radius: 50%;
  font-size: 3vh;
  background: hsl(345deg 100% 47%);
  color: white;
  transform: translateY(-6px);
  align-items: center;
  justify-content: center;
  will-change: transform;
  transition: transform 250ms;
  box-shadow: inset 0 5px 4px 3px rgb(239, 82, 118),
    inset 20px 2px 80px 2px rgba(225, 73, 131, 0.599);
}

.pushable:hover .front {
  transform: translateY(-8px);
  background: rgb(240, 17, 73);
}

.front:hover {
  box-shadow: inset 0 5px 3px 2px white,
    inset 20px 2px 80px 2px rgba(226, 127, 165, 0.599);
}
.front:active {
  box-shadow: inset 0 5px 3px 2px rgb(255, 155, 155),
    inset 20px 2px 80px 2px rgba(79, 42, 56, 0.599);
}

.pushable:active .front {
  transform: translateY(-2px);
}

.red-val {
  position: absolute;
  top: 2vh;
  transform: translateX(-8vw);
}
.green-val {
  position: absolute;
  top: 2vh;
  transform: translateX(0);
}
.blue-val {
  position: absolute;
  top: 2vh;
  transform: translateX(8vw);
}

.top-bar {
  display: flex;
  top: 0;
  height: 7vh;
  width: 100vw;
  background-color: rgb(0, 0, 0);
  position: fixed;
  box-shadow: inset 0 -1vh 10px -5px white, 0 1vh 10px -5px rgb(78, 78, 78);
  align-items: center;
  justify-content: center;
}
